.topBar {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  align-content: center;
  width: 100%;
  height: 8vh;
  position: fixed;
  z-index: 100;
  background-color: #132d3e;
}

.AVP {
  justify-content: flex-start;
  flex-direction: row;
  align-content: center;
  width: 50%;
  height: 100%;
}

.initial {
  color: white;
  /* font-family: 'Roboto Slab', serif; */
  font-family: 'Krona One', sans-serif;
  font-size: 2.4rem;
} 

.NavBar {
  display: flex;
  flex-wrap: nowrap;
  justify-content: flex-end;
  right: 0;
  width: 50%;
  height: 100%;
}

.navLink {
  display: inline-block;
  padding: 10px;
  color: white;
  margin-right: 2vw;
  
  font-size: 1.5rem;
}